.modal {
    z-index: 1000;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    overflow-x: hidden;

    &.active {
        opacity: 1;
        visibility: visible;
    }

    &__body {
        min-height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 30px 10px;
    }

    &__content {
        position: relative;
        background-color: #fff;
        color: #000;
        max-width: 800px;
        padding: 30px;
        border-radius: 10px;
    }

    &__close {
        position: absolute;
        top: 15px;
        right: 15px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: $accent;

        cursor: pointer;
    }

    &__close-icon {
        display: inline-block;
        position: absolute;
        left: 50%;
        top: 50%;
        bottom: auto;
        right: auto;
        transform: translateX(-50%) translateY(-50%);
        width: 18px;
        height: 2px;
        background-color: rgba(#fff, 0);

        border-radius: 1px;

        &::before, &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;

            border-radius: 1px;
            background-color: inherit;

            background-color: rgba(#fff, 1);
        }

        &:before {
            bottom: 0;
            transform: rotate(45deg);
        }

        &:after {
            top: 0;
            transform: rotate(-45deg);
        }
    }
}
.sign-in-form {
    text-align: center;

    &__title {
        margin: 0px 0px 20px 0px;
        font-size: 30px;
        font-weight: 700;
    }

    &__social {
        margin: 0px 0px 20px 0px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__social-item {
    }

    &__social-link {
    }

    &__text {
        margin: 0px 0px 20px 0px;
    }

    &__field {
        margin: 0px 0px 20px 0px;
    }

    &__input {
        background-color: #eee;
        width: 100%;
        padding: 12px 15px;
        margin: 0px 0px 10px 0px;
        border-radius: 20px;

        &:last-child {
            margin: 0;
        }
    }

    &__email {
    }

    &__password {
    }

    &__forgot-password-link {
        margin: 0px 0px 20px 0px;
    }

    &__btn {
        border-radius: 50px;
        font-size: 16px;

        &::before {
            box-shadow: none;
        }

        &:hover {
            background-color: #ce2e46;
        }
    }
}

.sign-in-modal__window.hystmodal__window {
    padding: 30px;
    border-radius: 10px;
}

.sign-in-modal__close.hystmodal__close {
