            left: 0;

            border-radius: 1px;
            background-color: inherit;

            background-color: rgba(#fff, 1);
        }

        &:before {
            bottom: 0;
            transform: rotate(45deg);
        }

        &:after {
            top: 0;
            transform: rotate(-45deg);
        }
}